<template>
  <div class="login">
    <!-- 背景动画 -->
    <div id="bacganim"></div>
  </div>
</template>

<script>
import * as PIXI from "pixi.js";
global.PIXI = PIXI;
require("pixi-spine");

export default{
  name:'login',
  data(){
    return{
      
    }
  },
  methods:{
    // 初始化背景动画
    initAnim(){
      var anim = new PIXI.Application({
        antialias:true,            
        transparent: true,
        // resizeTo: window
      });
      document.getElementById('bacganim').appendChild(anim.view);
      let loader = anim.loader.add('Serika','/spine/Serika_home.json')
      let Serika = null;
      loader.load((loader,res)=>{
        Serika = new PIXI.spine.Spine(res.Serika.spineData)
        let skeleton = Serika.skeleton;
        //获取骨骼
        let controlEye = skeleton.findBone('Touch_Eye')

        //眼位置
        let relative = {x: 0, y: 0}   
        //眼骨骼初始位置
        relative.x = controlEye.x
        relative.y = controlEye.y
        let elemt1 = document.querySelector("body")
        //移动
        elemt1.addEventListener('mousemove',function(e){
          controlEye.y = -(e.clientX - 700)/3
          controlEye.x = -(e.clientY - 300)/3
        })
        //过渡
        elemt1.addEventListener('mouseleave',function(e){
          let i = 1
          let timer = setInterval( function(){
            controlEye.y = controlEye.y - (controlEye.y - relative.y)*(i/10)
            controlEye.x = controlEye.x - (controlEye.x - relative.x)*(i/10)
            i++
            if(i===10){
                i=1
                clearInterval(timer)
            }
          }, 40);
        })
        Serika.state.setAnimation(0,'Idle_01',true);
        console.log(Serika);
        //Serika.alpha = 0.5;
        //const alphaFilter = new PIXI.filters.AlphaFilter(alpha:0.4)
        // const alphaFilter = new PIXI.filters.AlphaFilter();
        // Serika.filters = [alphaFilter]
        // alphaFilter.alpha = 0.3;
        // Serika.x = 500;
        // if (window.innerWidth < 767){
        //   Serika.x = window.innerWidth 
        // }
        Serika.y = -300;
        Serika.scale.x = 0.4;
        Serika.scale.y = 0.4;
        anim.stage.addChild(Serika);
      }); 
      // let viewWidth = document.getElementById('body').clientWidth;
      anim.renderer.view.style.position = "absolute";
      anim.renderer.view.style.display = "block";
      anim.renderer.autoResize = true;
      anim.renderer.resize(window.innerWidth-1, window.innerHeight-2);
      window.addEventListener('resize', ()=>{
        // console.log(window.innerWidth);
        this.$nextTick(() => {
          // console.log(document.getElementsByClassName('loginmain')[0].clientHeight);
          // if (window.innerWidth < 767){
          //   Serika.x = window.innerWidth 
          // }
          let h = document.getElementsByClassName('loginmain')[0].clientHeight;
          window.innerHeight > h ? anim.renderer.resize(window.innerWidth-1, window.innerHeight-2) : anim.renderer.resize(window.innerWidth-1, h-2)
        })
      });
    },
  },
  mounted(){
    // 初始化背景动画
    this.initAnim()
  },
  beforeDestroy(){
    window.removeEventListener('resize', ()=>{
      this.$nextTick(() => {
        let h = document.getElementsByClassName('loginmain')[0].clientHeight;
        window.innerHeight > h ? anim.renderer.resize(window.innerWidth-1, window.innerHeight-1) : anim.renderer.resize(window.innerWidth-1, h-1)
      })
    });
  }
}
</script>

<style scoped>
  /* 解决margin塌陷问题 */
  .login{
    height: 100%;
    width: 100%;
  }
  /* 背景动画 */
  #bacganim{
    width: 100%;
    height: 100%;
  }
  /* #bacganim canvas{
    width: 100%;
  } */
</style>